.popup {
  padding: 20px;
  width: 300px;
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  color: #333;
  transition: all 0.3s;
}

.popup.dark {
  background-color: #1c1c1c;
  color: #eee;
}

.popup h2, h3 {
  margin-bottom: 10px;
}

.popup button {
  margin-bottom: 15px;
  padding: 5px 10px;
  border: none;
  background-color: #ddd;
  cursor: pointer;
  border-radius: 4px;
}

.text-report {
  margin-top: 15px;
}
